/*CONTENT*/
.content {
    flex: 1;
    min-width: 300px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.write__post {
    width: 100%;
    max-width: 680px;
    background-color: var(--white-color);
    margin-top: 16px;
    border-radius: 8px;
}

.posts {
    width: 100%;
}

.post {
    width: 100%;
    max-width: 680px;
    background-color: var(--white-color);
    margin-top: 16px;
    border-radius: 8px;
}

/*POST HEADER*/
.post__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 60px;
    padding: 12px 16px;
}

.post__header-info {
    display: flex;
    align-items: flex-start;
}

.post__header-user-img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
}

.post__header-subinfo {
    display: flex;
    padding-left: 8px;
}

.post__header-user-name {
    padding-left: 8px;
    font-weight: 700;
}

.post__header-time {
    font-size: 12px;
    color: rgb(101, 103, 107);
}

.post__header-privacy {
    padding-left: 8px;
    font-size: 12px;
    color: rgb(101, 103, 107);
}

.post__header-modify {
    width: 36px;
    height: 36px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
}

.post__header-modify:hover {
    background-color: rgb(240, 242, 245);
    cursor: pointer;
}

/*POST CONTENT*/
.post__content {}

.post__content-text {
    padding: 4px 16px 16px 16px;
}

.post__content-media {
    position: relative;
    width: 100%;
    padding-top: 56.25%;
}

.post__content-media-iframe {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
}

/*POST ACTION*/
.post__action {
    padding: 10px 0;
    margin: 0 16px;
    color: rgb(101, 103, 107);
}

.post__action-info {
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: solid 1px #ced0d4;
    padding-bottom: 10px;
}

.post__action-info-like {
    flex-grow: 1;
}

.post__action-info-like-icon {
    color: #0062E0;
    margin-right: 8px;
}

.post__action-info-comment {
    flex-grow: 0;
    margin-right: 8px;
}

.post__action-info-share {
    flex-grow: 0;
}

.post__action-list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 44px;
    border-bottom: solid 1px #ced0d4;
}

.post__action-like,
.post__action-comment,
.post__action-share {
    flex: 1 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 18px;
}

.post__action-like-icon,
.post__action-comment-icon,
.post__action-share-icon {
    font-size: 18px;
}

/*POST COMMENT*/
.post__comment {
    padding: 16px 0;
    height: 300px;
    overflow-y: auto;
}

.post__comment-write {
    display: flex;
    align-items: center;
}

.post__comment-user-img {
    width: 32px;
    height: 32px;
    border-radius: 50%;
}

.post__comment-input {
    margin-left: 8px;
    flex-grow: 1;
}

.post__comment-input-form {
    border-radius: 16px;
    padding: 8px 12px;
    border: none;
    background-color: rgb(240, 242, 245);
    width: 90%;
}

.post__comment-input-form:focus {
    outline: none;
}

.post__comment-read {
    margin-top: 8px;
    display: flex;
    align-items: center;
    color: var(--black-color);
}

.post__comment-content {
    margin-left: 8px;
    background-color: rgb(240, 242, 245);
    border-radius: 16px;
}

.post__comment-user-name {
    padding: 8px 16px 0 16px;
    font-size: 14px;
    font-weight: 600;
}

.post__comment-text {
    padding: 0 16px 8px 16px;
}

/*POST COMMENT INTERUPT*/
.post__comment-interupt {
    display: none;
}

/*WRITE POST*/
.write__post-wrap {
    display: flex;
    align-items: center;
    margin: 8px;
    background-color: var(--white-color);
}

.write__post-img {
    width: 2.5rem;
    border-radius: 50%;
}
.write__post-input {
    border-radius: 16px;
    padding: 8px 12px;
    border: none;
    background-color: rgb(240, 242, 245);
    width: 90%;
    height: 24px;
    margin-left: 8px;
    font-size: 1rem;
}

.write__post-input:focus {
    outline: none;
}

.write__post-btn{
    width: 30%;
    align-self: flex-start;
    background-image: linear-gradient(35deg, rgb(0, 132, 255), yellow) ; 
    padding: 8px 16px;
    border-radius: 16px;
    color: var(--white-color);
    font-weight: 600;
    border: none;
    font-size: 1.25rem;
}

.write__post-media {
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 40px;
    padding: 8px 0;
    border-top: 1px solid var(--gray-color-2);
    margin-top: 12px;
}